/* About Section Styling */
#contact{
    text-align: center;
}
.shoot .eml{
color:#0026ff;
}

#about{
	background-color: #fff0f0 ;
}

 /* My Skills CSS */
.skills{
background: #ffffff;
}
.skills .content{
padding: 40px 0;
}
.skills .skills-details{
display: flex;
justify-content: space-between;
align-items: center;
}
.skills-details .text{
width: 50%;
}
.skills-details p{
color: #0E2431;
text-align: justify;
}
.skills .skills-details .experience{
display: flex;
align-items: center;
margin: 0 10px;
}
.skills-details .experience .num{
color: #0E2431;
font-size: 80px;
}
.skills-details .experience .exp{
color: #0E2431;
margin-left: 20px;
font-size: 18px;
font-weight: 500;
margin: 0 6px;
}
.skills-details .boxes{
width: 45%;
display: flex;
flex-wrap: wrap;
justify-content: space-between;
}
.skills-details .box{
width: calc(100% / 2 - 20px);
margin: 20px 0;
}
.skills-details .boxes .topic{
font-size: 20px;
color: #000000;
}
.skills-details .boxes .per{
font-size: 60px;
color: #000000;
}


/* contact form*/

#contact{
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;
	-webkit-box-flex:1;
	-ms-flex:1 1 35%;
	flex:1 1 35%;
	-webkit-box-orient:vertical;
	-webkit-box-direction:normal;
	-ms-flex-flow:column;
	flex-flow:column;
	justify-items:center;
	-ms-flex-line-pack:center;
	align-content:center;
	-webkit-box-align:center;
	-ms-flex-align:center;
	align-items:center;
	-webkit-box-pack:center;
	-ms-flex-pack:center;
	justify-content:center
}

.contact-form{
	justify-items:center;
	-ms-flex-line-pack:center;
	align-content:center;
	-webkit-box-align:center;
	-ms-flex-align:center;
	align-items:center;
	-webkit-box-pack:center;
	-ms-flex-pack:center;
	justify-content:center;
	border-radius:8px;
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;
	height:100%;
	width:90%;
	-webkit-box-orient:vertical;
	-webkit-box-direction:normal;
	-ms-flex-flow:column;
	flex-flow:column;
}

@media (min-width:710px){
.contact-form{
width:50%;
}
}
.contact-form label{
	color:#2d69a7;
	font-size:18px;
	font-weight:500;
	margin-bottom:.1rem;
	margin-top:1.5rem;
	font-family:'Courier New',Courier,monospace
}
.contact-form input{
	border:none;
	width:100%;
	border-radius:8px;
	font-family:'Source Sans Pro';
	padding:10px;
	background-color:#2d79b8;
	color:#fff;
	-webkit-box-shadow:inset 0 7px 7px rgba(14,14,14,.5);
	box-shadow:inset 0 7px 7px rgba(14,14,14,.5)
}
.contact-form input::-webkit-input-placeholder{
	font-family:'Source Sans Pro';
	font-size:15px;
	font-weight:100;
	color:#dadada;
	font-style:italic
}
.contact-form input::-moz-placeholder{
	font-family:'Source Sans Pro';
	font-size:15px;font-weight:100;
	color:#dadada;font-style:italic
}
.contact-form input:-ms-input-placeholder{
	font-family:'Source Sans Pro';
	font-size:15px;
	font-weight:100;
	color:#dadada;
	font-style:italic
}
.contact-form input::-ms-input-placeholder{
	font-family:'Source Sans Pro';
	font-size:15px;
	font-weight:100;
	color:#dadada;
	font-style:italic
}
.contact-form input::placeholder{
	font-family:'Source Sans Pro';
	font-size:15px;
	font-weight:100;
	color:#dadada;
	font-style:italic
}
.contact-form .input-field{
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;width:80%;
	-webkit-box-orient:vertical;
	-webkit-box-direction:normal;
	-ms-flex-flow:column;
	flex-flow:column;
	-webkit-box-align:start;
	-ms-flex-align:start;
	align-items:flex-start
}
.contact-form textarea{
	border:none;
	font-family:'Source Sans Pro';
	-webkit-box-shadow:inset 0 7px 7px rgba(14,14,14,.5);
	box-shadow:inset 0 7px 7px rgba(14,14,14,.5);
	padding:8px;background-color:#2d79b8;
	color:#fff;
	width:100%;
	height:150px;
	border-radius:8px
}
.contact-form textarea::-webkit-input-placeholder{
	font-family:'Source Sans Pro';
	font-size:15px;
	color:#dadada;
	font-weight:100;
	font-style:italic
}
.contact-form textarea::-moz-placeholder{
	font-family:'Source Sans Pro';
	font-size:15px;
	color:#dadada;
	font-weight:100;
	font-style:italic
}
.contact-form textarea:-ms-input-placeholder{
	font-family:'Source Sans Pro';
	font-size:15px;color:#dadada;font-weight:100;font-style:italic
}
.contact-form textarea::-ms-input-placeholder{
	font-family:'Source Sans Pro';
	font-size:15px;
	color:#dadada;
	font-weight:100;
	font-style:italic
}
.contact-form textarea::placeholder{
	font-family:'Source Sans Pro';
	font-size:15px;
	color:#dadada;
	font-weight:100;
	font-style:italic
}
.contact-form .btn{
	margin:25px 0
}
@media (max-width:992px){
#contact{
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;
	-webkit-box-flex:1;
	-ms-flex:1 1 80%;
	flex:1 1 80%
	}
}

#bottom-container{
	position:absolute;
	bottom:18vh;
	width:100%
}
@media (max-width:992px){
#bottom-container{
		position:absolute;
		bottom:80vh;
		width:100%
	}
