/* contact form*/

.top-section{
padding-top: 16%;
padding-bottom: 10%
}
@media (max-width:560px)  {
.top-section{
padding-top: 30%;
}
}

section .content{
width: 80%;
margin: 40px auto;
font-family: 'Poppins', sans-serif;
text-align: center;
}
.about .about-details{
display: flex;
justify-content: space-between;
align-items: center;
}
section .title{
display: flex;
justify-content: center;
margin-bottom: 20px;
}
section .title span{
color: #000000;
font-size: 30px;
font-weight: 600;
position: relative;
padding-bottom: 8px;
}
section .title span::before,
section .title span::after{
content: '';
position: absolute;
height: 3px;
width: 100%;
background: #ff0000;
left: 0;
bottom: 0;
}
section .title span::after{
bottom: -7px;
width: 70%;
left: 50%;
transform: translateX(-50%);
}
.about .about-details .left{
width: 45%;
}
.about .left img{
height: 400px;
width: 400px;
object-fit: cover;
border-radius: 12px;
}
@media(min-width:1000px) and (max-width: 1090px){
.about .left img{
width: 300px;
}
}
.about-details .right{
width: 55%;
}
section  .topic{
color: #000000;
font-size: 25px;
font-weight: 500;
margin-bottom: 10px;
}
.about-details .right p{
text-align: justify;
color: rgb(0, 0, 0);
}
section .button{
margin: 16px 0;
text-align: center;
}
section .button button, .cv-button{
outline: none;
padding: 10px 17px;
border-radius: 10px;
font-size: 20px;
font-weight: 400;
background: #ff0000;
border: 2px solid transparent;
cursor: pointer;
transition: all 0.4s ease;
color:#ffffff;
}

.description .viewer-btn{
display:flex;
flex-direction: row;
}

.description .view-button{
width:125px;
}
.description .view-button1{
width:180px;
}

.description .view-button .source-button{
outline: none;
padding: 6px 10px;
border-radius: 10px;
font-weight: 400;
background: #2d79b8;
border: 2px solid transparent;
cursor: pointer;
transition: all 0.4s ease;
color:#ffffff;
}

.description .view-button1 .source-button{
outline: #ff0000;
padding: 6px 10px;
border-radius: 10px;
font-weight: 400;
background: #fff;
border: 2px solid #2d79b8;
cursor: pointer;
transition: all 0.4s ease;
color:#2d79b8;
}

.description .tech-used{
margin-top:25px;
}

@media (max-width: 500px){
.description .viewer-btn{
flex-direction: column;
}
.description .view-button {
margin-bottom: 20px;
}
}

.project-button{
margin-top: 150px;
text-align: center;
}

button .btn:hover, .cv-button:hover, .source-button:hover{
color: #10355b !important;
background: #a7b6c3 !important;
}

.btn:hover{
background-color: #a7b6c3;
color: #ffffff;
}

